import moment from 'moment';

export default function daysRemaining(bday, today) {
  const thisYearBday = moment({
    year: today.year(),
    month: bday.month(),
    day: bday.date(),
  });

  today.startOf('day');

  const thisYearDuration = moment.duration(thisYearBday.diff(today));
  let days = thisYearDuration.asDays();

  if (days < 0) {
    const nextYearBday = moment({
      year: today.year() + 1,
      month: bday.month(),
      day: bday.date(),
    });

    const nextYearDuration = moment.duration(nextYearBday.diff(today));
    days = nextYearDuration.asDays();
  }

  return days;
}
