import React, { useState } from 'react';
import Enter from './pages/enter';
import BDay from './pages/bday';
import daysRemaining from './pages/waiting';
import moment from 'moment';
import WaitingScreen from './pages/waitingScreen';

function App() {
  const [lightsOn, lightHandler] = useState(false);

  const nextBday = daysRemaining(moment([2001, 7, 5]), moment());

  return (
    <div className='App'>
      {nextBday === 0 ? (
        lightsOn ? (
          <BDay />
        ) : (
          <Enter onSwitchClick={() => lightHandler(true)} />
        )
      ) : (
        <WaitingScreen days={nextBday} />
      )}
    </div>
  );
}

export default App;
